import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/orderQuery',
      name: 'orderQuery',
      component: () => import('@/pages/orderQuery')
    },

    {
      path: '/life',
      name: 'life',
      component: () => import('@/pages/life')
    },
    {
      path: '/leisureTime',
      name: 'leisureTime',
      component: () => import('@/pages/leisureTime')
    },
    {
      path: '/hotel',
      name: 'hotel',
      component: () => import('@/pages/hotel')
    },
    {
      path: '/HotelList',
      name: 'HotelList',
      component: () => import('@/pages/HotelList')
    },
    {
      path: '/paymentSuccess',
      name: 'paymentSuccess',
      component: () => import('@/pages/payment/paymentSuccess')
    },
    {
      path: '/orderConfirm/:payType',
      name: 'orderConfirm',
      component: () => import('@/pages/orderConfirm')
    },

    {
      path: '/OffLine',
      name: 'OffLine',
      component: () => import('@/pages/OffLine')
    },
  ]
});



