import Vue from 'vue';
import 'vue-cookies'; // window.$cookies

import http from './utils/http';
import api from './utils/api';

let userInfo = window.$cookies.get("userInfo") || {};

const $wx = Vue.prototype.$wx = {
  login(redirectUrl) {
    window.location.href = `${api.wxLogin}?url=${redirectUrl}`;
  },
  isLogin() {
    return !!userInfo.openid;
  },
  getUserInfo() {
    if (!this.isLogin()) {
      this.init();
    }

    return userInfo;
  },
  getOpenId() {
    return this.getUserInfo().openid;
  },
  async init(currentRoutePath = location.hash.slice(1)) {
    console.info('当前回调地址为：%s', currentRoutePath); // 仅支持hash模式

    // init userInfo
    if (!this.isLogin()) {
      const parseWxStatus = function () {
        let search = window.location.href.split('?')[1];
        if (!search) {
          return null;
        }
        search = search.split('&');

        const query = {};
        search.forEach(item => {
          const kv = item.split('=');
          query[kv[0]] = kv[1];
        });

        console.log(query);
        const { code, state } = query;
        if (code && state) {
          return { code, state };
        }

        return null;
      };

      const wxStatus = parseWxStatus();
      if (!wxStatus) {
        this.login(currentRoutePath); // 页面打开调用init --> 登录，跳转回来 --> 页面打开调用init
        return;
      }

      const res = await http.get(api.getInfo, {
        code: wxStatus.code
      });

      if (res.status === 200) {
        userInfo = res.data;
        window.$cookies.set("userInfo", userInfo); // 因为每个code只能使用一次
      }
    }

    // 其它。。。
  },
};




